.control-panel .noUi-handle,
.control-panel .noUi-horizontal .noUi-handle {
  box-shadow: none;
  box-sizing: border-box;
  position: absolute;
  cursor: pointer;
  pointer-events: inherit;
  top: 0px;
  left: 4.94876%;
  z-index: 1;
  margin: 1px 0px 0px;
  width: 12px;
  height: 12px;
  background-color: #0b9aab;
  background-clip: padding-box;
  border: 0px solid transparent;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: background 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms, border-color 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms, width 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms, height 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  overflow: visible;
  outline: none;
}

.control-panel .noUi-horizontal {
  height: 2px;
}

.control-panel .axis-slider .noUi-horizontal {
  height: 4px;
}

.control-panel .noUi-target {
  border: none;
}

.control-panel .axis-slider .noUi-handle:before,
.control-panel .noUi-handle:before,
.control-panel .noUi-handle:after,
.control-panel [disabled].axis-slider .noUi-handle:after,
.control-panel .noUi-handle:after,
.control-panel .axis-slider .noUi-handle:after,
.control-panel .noUi-handle:after,
.control-panel .noUi-handle:before {
  display: none;
  content: none;
}

.drawer-closed .control-panel {
  width: 0;
  min-width: 0px;
}

.control-panel {
  flex: 1 auto;
  order: 1;
  overflow-y: scroll;
  max-width: 540px;
}

.control-panel .card-title {
  padding: 2px;
}

.control-panel .ant-card-body {
  padding: 0;
}

.control-panel .ant-card-extra .ant-btn {
  margin-right: 1em;
}

@media only screen and (max-width: 800px) {
  .control-panel {
    max-width: 370px;
  }
}




