#cell-viewer body {
  font-family: 'Overpass-Light';
}

#cell-viewer h1,
#cell-viewer h2,
#cell-viewer h3,
#cell-viewer h4 {
  font-family: 'Merriweather Sans';
}

#cell-viewer p {
  font-weight: 300;
  font-size: 1rem;
}

html,
body,
#cell-viewer {
  height: 100%;
  width: 100%;
  display: flex;
  font-family: 'Overpass';
}

.cell-viewer-app {
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
  flex-flow: column;
  flex: 1 1 auto;
}

.cell-viewer-app .header-wrapper {
  z-index: 99;
  box-shadow: 0px 0px 3px 0px grey;
  position: relative;
}

.cell-viewer-app .cell-viewer-wrapper {
  right: 0;
  flex: 10 0px;
  order: 2;
  position: relative;
  display: flex;
  overflow: hidden;
}




